<template>

    <div class="bdmap">

<mt-header title="">
  <router-link to=" " slot="left">
    <mt-button icon="back" @click="goback"></mt-button>
  </router-link>
  <mt-button icon="more" slot="right"></mt-button>
</mt-header>

  


    <div id="allmap" style="height:300px"></div>
    <input type="text"  v-model="start">
    <input type="text"  v-model="end">
    <input type="button" value="" @click="search">
    <input type="text" v-model="near">
    <input type="button" value="" @click="nearby">
    </div>




</template>
<script>
import { Header } from 'mint-ui'
export default {
     data(){
    return {
      map:null,
      marker:null,
      start:" ",
      end:" ",
      near:"",
      point:null,
      nav:false
    }
  },
    mounted() {
       this.map = new BMap.Map("allmap");    // Map
    this.point=new BMap.Point(121.4981469221, 31.3843514270);
    this.map.centerAndZoom(this.point, 16);  // ,
    this.marker = new BMap.Marker(this.point);  // 
    this.map.addOverlay(this.marker); // 
    this.marker.setAnimation(BMAP_ANIMATION_BOUNCE) //
  //   setTimeout(function(){
	// 	map.setZoom(18);   
	// }, 2000);

    },
    methods:{
    search(){
      console.log('search')
      this.map.clearOverlays();
    this.map.enableScrollWheelZoom(true);
      var walking = new BMap.WalkingRoute(this.map, {renderOptions:{map: this.map, autoViewport: true}});
        walking.search(this.start, this.end);
         },
     
    nearby(){
      var local =  new BMap.LocalSearch(this.map, {renderOptions: {map: this.map, autoViewport: false}});  
      this.map.clearOverlays();
      local.searchNearby(this.near,this.point,1000);
    },
      goback(){
      this.$router.go(-1)
    }
  }
}

</script>
<style >
    .bdmap{text-align:center;}
    mt-header{padding-top:30px;}
  
</style>