export const date =  time=>{

  let d = new Date();
  d.setTime(time);
  let year = d.getFullYear();
//  let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
  // let year = d.getFullYear();
 let month = d.getMonth() + 1; 
        let day  = d.getDate();
        let hour  = d.getHours()

  return ''+year+''+month+''+day +''+hour +''
}